<template>
  <div id="header">
    <table id="tableHeader">
        <tr>
          <td id="leftContent">
            <!-- blank line -->
          </td>

          <td id="centerContent">
            <h3><span class="differentColor">E</span>dward <span class="differentColor">S</span>ujono</h3>
          </td>

          <td>
            <table id="rightContent">
              <tr>
                <td id="abooutHeader"  v-bind:style="{'border-bottom': aboutBorder}" v-on:click="clickAbout" ><router-link to="/" id="aboutHeader" class="removeA" >About</router-link></td>
                <td id="workHeader"  v-bind:style="{'border-bottom': projectBorder}" v-on:click="clickProject"><router-link to="/Project" class="removeA">Project</router-link></td>
                <td id="internHeader" v-bind:style="{'border-bottom': internshipBorder}" v-on:click="clickInternship"><router-link to="/Internship" class="removeA"  >Internship</router-link></td>
                <td id="contactHeader" v-bind:style="{'border-bottom': contactBorder}" v-on:click="clickContact"><router-link to="/Contact" class="removeA" >Contact</router-link></td>
              </tr>
            </table>
          </td>
        </tr>
    </table>
  </div>
</template>

<script>

  export default {

    name: 'header',
    data: function(){
      return {
          aboutBorder: "2px solid white",
          projectBorder: "",
          internshipBorder: "",
          contactBorder: ""
      }
    },
    methods: {
        clickAbout: function (events) {
            this.aboutBorder =  "2px solid white";
            this.projectBorder = "";
            this.internshipBorder = "";
            this.contactBorder = "";

        },
        clickProject: function(events){
          console.log("project", "clicked");
          this.aboutBorder =  "";
          this.projectBorder = "2px solid white";
          this.internshipBorder = "";
          this.contactBorder = "";
        },
        clickInternship: function(event){
          this.aboutBorder =  "";
          this.projectBorder = "";
          this.internshipBorder = "2px solid white";
          this.contactBorder = "";
        },
        clickContact: function(event){
          this.aboutBorder =  "";
          this.projectBorder = "";
          this.internshipBorder = "";
          this.contactBorder = "2px solid white";
        }
    }

  }

</script>

<style scoped="">

  #header{
    background-color: rgba(239, 83 ,90, 0.7);
    width: 100%;
    height: 50px;
    padding-bottom: 10px;
  }

  #tableHeader{
    z-index: 2;
    width: 100%;
    height: 50px;
  }

  #leftContent {
    width: 25%;
  }

  #centerContent {
    width: 50%;
  }

  #rightContent {
    width: 100%;
  }

  #rightContent tr td {
    cursor: pointer;
    padding: 10px;
  }

  #rightContent tr td:hover {
    opacity: 1;
    color: #FFF;
  }

  .differentColor {
    color: #FFF;
    font-size: 25px;
  }

  #workHeader {
    opacity: 1;
    color: #FFF;
  }


  .removeA {
    text-decoration: none;
    color:  #83afdf;
  }

  .removeA:hover {
    color: #FFF;
  }


</style>



