<template>
  <div id="work">

      <!-- fitbeats bubble -->
      <div id="bubbleContainerFitbeats" v-on:click="showModalFitbeats">
        <Fitbeats class="bubble"></Fitbeats>
      </div>
      <sweet-modal modal-theme="dark" overlay-theme="dark" ref="modalFitbeats">
        <div>
          <FitbeatsModal></FitbeatsModal>
        </div>
      </sweet-modal>

    <!-- pintu singapura bubble -->
    <div id="bubbleContainerPintuSingapura" v-on:click="showModalPintuSingapura">
      <PintuSingapura class="bubble"></PintuSingapura>
    </div>
    <sweet-modal modal-theme="dark" overlay-theme="dark" ref="modalPintuSingapura">
      <div>
        <PintuSingapuraModal></PintuSingapuraModal>
      </div>
    </sweet-modal>


    <!-- ICN Bubble -->
    <div id="bubbleContainerIcn" v-on:click="showModalIcn">
      <Icn class="bubble"></Icn>
    </div>
    <sweet-modal modal-theme="dark" overlay-theme="dark" ref="modalIcn">
      <div>
        <IcnModal></IcnModal>
      </div>
    </sweet-modal>

    <!-- First Ureca  -->
    <div id="bubbleContainerFirstUreca" v-on:click="showModalUreca">
      <FirstUreca class="bubble"></FirstUreca>
    </div>
    <sweet-modal modal-theme="dark" overlay-theme="dark" ref="modalFirstUreca">
      <div>
        <FirstUrecaModal></FirstUrecaModal>
      </div>
    </sweet-modal>

    <!-- Second Ureca  -->
    <div id="bubbleContainerSecondUreca" v-on:click="showModalUrecaSecond">
      <SecondUreca class="bubble"></SecondUreca>
    </div>
    <sweet-modal modal-theme="dark" overlay-theme="dark" ref="modalSecondUreca">
      <div>
        <SecondUrecaModal></SecondUrecaModal>
      </div>
    </sweet-modal>

    <!-- MiniGame -->
    <div id="bubbleContainerMiniGame" v-on:click="showModalMiniGame">
      <MiniGame class="bubble"></MiniGame>
    </div>
    <sweet-modal modal-theme="dark" overlay-theme="dark" ref="modalMiniGame">
      <div>
        <MiniGameModal></MiniGameModal>
      </div>
    </sweet-modal>



  </div>
</template>

<script>
  import { SweetModal, SweetModalTab } from 'sweet-modal-vue'
  // for fitbeats bubble
  import Fitbeats from '@/components/Fitbeats'
  import FitbeatsModal from '@/components/FitbeatsModal'
  // for pintusingapura bubble
  import PintuSingapura from '@/components/PintuSingapura'
  import PintuSingapuraModal from '@/components/PintuSingapuraModal'
  // for ICN bubble
  import Icn from '@/components/Icn'
  import IcnModal from '@/components/IcnModal'
  // for First Ureca
  import FirstUreca from '@/components/FirstUreca'
  import FirstUrecaModal from '@/components/FirstUrecaModal'
  // for second Ureca
  import SecondUreca from '@/components/SecondUreca'
  import SecondUrecaModal from '@/components/SecondUrecaModal'
  // for MiniGame
  import MiniGame from '@/components/MiniGame'
  import MiniGameModal from '@/components/MiniGameModal'

  export default {

    name: 'about',
    components: {
      Fitbeats,
      SweetModal,
      SweetModalTab,
      FitbeatsModal,
      PintuSingapura,
      PintuSingapuraModal,
      Icn,
      IcnModal,
      FirstUreca,
      FirstUrecaModal,
      SecondUreca,
      SecondUrecaModal,
      MiniGame,
      MiniGameModal
    },

    methods: {
        showModalFitbeats: function (event){
          this.$refs.modalFitbeats.open();
        },
        showModalPintuSingapura: function (event){
          this.$refs.modalPintuSingapura.open();
        },
        showModalIcn: function (event){
          this.$refs.modalIcn.open();
        },
        showModalUreca: function (event){
            this.$refs.modalFirstUreca.open();
        },
        showModalUrecaSecond: function(event){
          this.$refs.modalSecondUreca.open();
        },
        showModalMiniGame: function(event){
          this.$refs.modalMiniGame.open();
        }
    }

  }

</script>

<style scoped="">

  #work{
    width: 100%;
    height: 100%;
  }

  #work table {
    margin-left: 30px;
    margin-right: 30px;
    width: 97%;
  }

  #work table tr {
    width: 90%;
  }

  #work table tr td {
    width: 30%;
  }

  .bubble {
    width: 500px;
    height: 500px;
    border-radius: 50%;
    -moz-animation:bounce 3s linear;
    -webkit-animation:bounce 3s linear;
    animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
    -o-animation-iteration-count: infinite;
    cursor: pointer;
  }

  @-moz-keyframes bounce {
    0%{ -moz-transform:scale(1); opacity:1;}
    50%{ -moz-transform:scale(1.2); opacity:0.7; }
    75%{ -moz-transform:scale(0.7); opacity:0.9;}
    100%{ -moz-transform:scale(1); opacity:1;}
  }

  @-webkit-keyframes bounce {
    0%{ -webkit-transform:scale(1); opacity:1;}
    50%{ -webkit-transform:scale(1.2); opacity:0.7;}
    75%{ -webkit-transform:scale(0.7); opacity:0.9;}
    100%{ -webkit-transform:scale(1); opacity:1;}
  }


</style>



